* {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  background-color: #EAECEE !important;
}

#root {
  display: flex;
  flex-direction: column;
}

#dashboard {
  flex: 1 1;
  padding: 17px 16px 8px 16px;
  box-sizing: border-box;
  width: 100%;
  display: block;
  float: left;
}

#dashboard main {
  display: flex;
  flex-direction: column;
}

.loading-plot {
  position: absolute;
  display: flex;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.5);
  opacity: 0.875;

  & > * {
    margin: auto;
  }
}

.timeline {
  display: flex;
  padding: 5px 1.5rem 0 0;
  box-sizing: border-box;
  align-items: start;
  .play {
    margin-right: 0.25rem;
    width: 41px;
    height: 41px;
    svg {
      width: 36px;
      height: 36px;
    }
  }
  .slider {
    flex: 1 1;
    .MuiSlider-root {
      padding-top: 20px;
      .MuiSlider-markLabel {
        top: 40px;
      }
    }
  }
}

#dashboard aside {
  text-align: center;
  .controls {
    display: inline-block;
    text-align: left;
    .plot-type {
      text-align: center;
      margin-top: 1px;
    }
  }
}

.section {
  height: 100%;
  width: 100%;
  padding: 10px;
  background-color: #FAFBFC !important;
  box-sizing: border-box;
}

.axis line {
  stroke: #000;
}

.axis text {
  fill: #000;
}

.tooltip, .share-modal {
  background-color: #FAFBFC !important;
}

.tooltip {
  z-index: 9999;  /* Appear over the app bar */
}

.share-modal {
  display: inline-block;
  position: fixed;
  right: 10px;
  top: 10px;
  padding: 10px;
  border-radius: 3px;
  border: thin solid #777;
}

.tooltip-content {
  padding: 8px;
  color: #222;

  h3 {
    font-weight: normal;
    margin: 0;
    padding: 0 0 8px 0;
  }

  div {
    opacity: 0.75;
  }
}

#plot-area {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

#plot {
  flex: 1 1;
}

.plot-label {
  fill: #202122;
  opacity: 0.9;
  font-size: 0.9rem;
}

.plot-header {
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: thin solid #222;
  .title {
    font-weight: 600;
  }
}

.MuiDrawer-root .MuiPaper-root {
  background-color: #EAECEE !important;
}

.side-menu {
  width: 270px;
  font-size: 1.2rem;
  color: #393939;

  a {
    display: flex;
    text-decoration: none;
    color: inherit;
  }

  .menu-item-icon {
    min-width: 46px;
    padding-left: 4px;
  }

  .nested-menu {
    .MuiList-padding {
      padding: 0.25rem 0;
    }
    .MuiListItem-root {
      padding-top: 0;
    }
    span {
      padding-left: 47px;
      font-size: 0.85rem;
    }
  }
}

.dialog-header {
  width: 100%;
  margin-left: 28px;
  box-sizing: border-box;
  display: flex;
  .dialog-back, .dialog-title {
    padding: 23px 0 8px 0;
    font-size: 1rem;
  }

  .dialog-title {
    font-weight: bold;
    opacity: 0.67;
    flex: 1 1;
    text-align: center;
  }
}

.share-dialog {
  .MuiCollapse-wrapper {
    max-height: 100% !important;
  }
  .MuiCollapse-root {
    overflow: scroll;
    height: 100% !important;
  }

  .share-dialog-content {
    background-color: #EAECEE;
    display: flex;
    flex-direction: column;
    max-height: 100%;
    min-width: 290px;


    .MuiDialogContent-root {
      padding-top: 0 !important;
    }

    .share-body {
      flex: 1 1;

      .preview-label {
        display: block;
        text-align: center;
        transform: scale(0.75);
      }

      .preview {
        display: block;
        margin: 5px auto 10px auto;
      }

      .row {
        justify-content: space-around;

        .MuiFormControl-root {
          margin-left: 10px;
          margin-right: 10px;
        }
      }

      .share-controls {
        text-align: center;

        .size-type-selector {
          flex-wrap: nowrap !important;
          flex-direction: row !important;
        }

        .size-inputs {
          justify-content: space-evenly;
          display: grid;
          margin-top: 12px;

          .custom-size-input {
            width: 4rem;
            margin-right: 20px;
          }
        }
      }
    }

    .note {
      text-align: center;
      font-size: 0.9em;
      color: #555;
    }
  }
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #3D3D3D !important;
  }
  .section {
    background-color: #303030 !important;
  }
  .axis line {
    stroke: #FFF;
  }
  .axis text, .plot-label {
    fill: #FFF;
  }
  .tooltip, .share-modal {
    background-color: #303030 !important;
    border: thin solid #717273;
    box-shadow: none;
  }
  .tooltip-content {
    color: #FFF;
  }
}

@media screen and (max-width: 639px) { /* Mobile */
  #dashboard main {
    height: 100vw;
    max-height: 420px;
  }
  #dashboard aside {
    padding-top: 10px;
  }
}

@media screen and (min-width: 640px) { /* Desktop */
  #root {
    height: 100%;
  }

  .top-bar {
    padding-right: 220px;
  }

  #dashboard main {
    width: 100%;
    height: 100%;
    float: left;
    margin-right: -220px;
    padding-right: 220px;
  }

  #dashboard aside {
    height: 100%;
  }

  #dashboard .controls > div {
    display: block;
  }
}